<template>
  <Column>
    <Row class="font-32 p-l-30 p-r-30 h-92 border-b w-100b bold" y="c">{{ $t('home.spotIndex') }}</Row>
    <Row class="w-100b p-l-30 p-r-30 h-60 c-info" x="sb">
      <div class="f-1" x="s">{{ $t('home.tradingPair') }}</div>
      <Row class="f-1" x="e">{{ $t('home.price') }}</Row>
      <Row class="f-size-210" x="e">{{ $t('home.riseFall') }}</Row>
    </Row>
    <div class="p-l-30 p-r-30 w-100b border-box">
      <Row v-for="(item,index) in list" :key="index" class="w-100b h-110 border-b" x="sb">
        <Column class="f-1" x="s">
          <div>
            <span class="font-32 m-r-4 bold">{{ item.coinMarket.split('/')[0] }}</span>
            <span class="c-info">/{{ item.coinMarket.split('/')[1] }}</span>
          </div>
          <div class="font-22 c-info m-t-4">{{ $t('home.tfh') }} {{ $f.keepDot(item.turnover,2) }}</div>
        </Column>
        <Column class="f-1" x="e">
          <div class="font-32 bold">{{ item.lastPrice }}</div>
          <div class="c-info font-22">${{ item.lastPrice }}</div>
        </Column>
        <Column class="f-size-210" x="e">
          <Row :class="[handleRiseFall(item.rangeAbility),'rise-fall','bold']" x="c" y="c">{{ $f.pnFill(NP.times(item.rangeAbility,100)) }}%</Row>
        </Column>
      </Row>
    </div>

  </Column>
</template>
<script setup>

const props = defineProps({
  list: {}
})

const handleRiseFall = (num) => {
  if (num == 0) {
    return 'sl-zero'
  } else if (num > 0) {
    return 'sl-up'
  } else {
    return 'sl-down'
  }
}

onMounted(() => {
})

</script>
