<template>
  <Column class="HomeSpotSwiper spot-swiper border-box h-180 p-t-15" x="c" y="c">
    <swiper
      :slidesPerView="1"
      :pagination="true"
      :modules="modules"
      :freeMode="true"
      :autoplay="{
        delay: 30000,
      }"
      class=" w-690 h-198 "
    >
      <swiper-slide v-for="(item,index) in lists" :key="index">
        <Row x="s">
          <Column v-for="(innerItem,idx) in item" :key="idx" class=" w-210 h-130 m-r-20" y="sb">
            <Row class="font-22 hss-title">
              <span class="bold">{{ innerItem.coinMarket }}</span>
              <span class="m-l-8" :class="$f.priceClass(innerItem.rangeAbility)">{{ $f.pnFill(NP.times(innerItem.rangeAbility,100)) }}%</span>
            </Row>
            <div class="w-210 m-t-6 bold c2" :class="$f.priceClass(innerItem.rangeAbility)">{{ innerItem.lastPrice }}</div>
            <div class="font-22 c-info hss-title">≈{{ innerItem.lastPrice }}</div>
          </Column>
        </Row>
      </swiper-slide>
    </swiper>
  </Column>
</template>
<script setup>
import { Swiper, SwiperSlide } from 'swiper/vue'
import { FreeMode, Autoplay, Pagination, Navigation } from 'swiper'
import 'swiper/css'
import 'swiper/css/effect-fade'
import 'swiper/css/navigation'
import 'swiper/css/pagination'
import { EffectFade } from 'swiper'

const modules = $ref([FreeMode, Autoplay, Pagination, Navigation])

const props = defineProps({
  list: {}
})

const lists = $ref([])

let raw = $ref([])

const handleSpot = () => {
  lists.push(raw.splice(0, 3))
  if (raw.length) {
    handleSpot()
  }
}

const tickersOnTop = async () => {
  raw = await homeApi.tickersOnTop()
  handleSpot()
}

onMounted(async () => {
  await tickersOnTop()
  var len = document.getElementsByClassName('c2')
  var size = 38
  for (var i = 0; i < len.length; i++) {
    var fontLen = len[i].innerHTML.length
    for (size = 38; size > 8; size--) {
      if (size * fontLen < len[i].offsetWidth || size == 9) {
        len[i].style.fontSize = size + 'px'
        break
      }
    }
  }
})

</script>
