<template>
  <Row class="h-88 p-l-30 p-r-30" x="sb">
    <img class="w-56 h-56" src="@/image/home/userIcon.png" @click="toRouter('/user/register')">
    <img class="w-188 h-50" src="@/image/home/bifinanceMenu.png">
    <Row>
      <Row @click="showPicker = true">
        <img class="w-36 h-36" src="@/image/home/language.png">

        <Row class="m-l-10 bold">
          <span>{{ curLang }}</span>
        </Row>
      </Row>

      <van-popup v-model:show="showPicker" round position="bottom">
        <van-picker
          v-model="fieldValue"
          :columns="columns"
          :confirm-button-text="$t('home.confirm')"
          :cancel-button-text="$t('home.cancel')"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>

    </Row>
  </Row>
</template>
<script setup>

let fieldValue = $ref([localStorage.locale])
const showPicker = $ref(false)

const arr = $ref(['en_us'])

const onConfirm = ({ selectedOptions }) => {
  // showPicker = false
  fieldValue = selectedOptions
  localStorage.locale = selectedOptions[0].value
  replaceParamVal('locale', selectedOptions[0].value)
  location.reload()
}
const columns = [
  { text: '中文', value: 'zh_cn' },
  { text: 'English', value: 'en_us' },
]

const curLang = $computed(() => {
  return columns.filter((obj) => obj.value == fieldValue)[0]?.text
})

</script>
