<template>
  <div>
    <Header />
    <Column class="m-b-30">
      <swiper
        :spaceBetween="30"
        :navigation="false"
        :pagination="false"
        :modules="modules"
        :autoplay="{
          delay: 2500,
          disableOnInteraction: false,
        }"
        class="mySwiper w-690 h-300 radius-10"
      >
        <swiper-slide v-for="(item,index) in urls" :key="index" class="bg-info bg-bnner" :style="{backgroundImage:`url(${item.imgUrl})`}" />
      </swiper>
    </Column>

    <el-carousel direction="vertical" height="20px" class="" :autoplay="true">
      <el-carousel-item v-for="(item,index) in announcementsList" :key="item.id" class="">
        <Row class="p-l-30 p-r-30 w-100b h-34" x="sb" y="c" @click="toLink(item.url)">
          <Row>
            <img class="w-42 h-32 m-r-10" src="@/image/home/Volume.png">
            <span class="font-24 bold gonggao w-600">{{ item.title }}</span>
          </Row>
          <van-icon name="arrow" size="18" />
        </Row>
      </el-carousel-item>
    </el-carousel>

    <div class="m-l-30 m-r-30">
      <action />
    </div>

    <spotSwiper v-if="newSpots.length" :list="newSpots" />
    <Row class="p-t-26 p-b-28 buy-coin-quickly" x="sb" @click="toRouter('/user/register')">
      <Row>
        <img class="w-88 h-88 m-r-30" src="@/image/home/buyCoin.webp">
        <Column>
          <div class="font-28 c-black bold">{{ $t('home.quicklyBuyCoins') }}</div>
          <div class="font-24 c-info">{{ $t('home.morePay') }}</div>
        </Column>
      </Row>
      <Row>
        <img class="w-88 h-88 m-r-30" src="@/image/home/newCoin.webp">
        <Column>
          <div class="font-28 c-black bold">{{ $t('home.newCoinTitle') }}</div>
          <div class="font-24 c-info">{{ $t('home.newCoinDdescript') }}</div>
        </Column>
      </Row>
      <!-- <img class="w-50 h-50" src="@/image/home/more.png"> -->
    </Row>

    <soptList v-if="rawSpots.length" :list="rawSpots" />
    <FooterDownload />
  </div>
</template>
<script setup>
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Autoplay, Pagination, Navigation } from 'swiper'
import 'swiper/css'

import 'swiper/css/effect-fade'
import 'swiper/css/navigation'
import 'swiper/css/pagination'

import { EffectFade } from 'swiper'

import { action, spotSwiper, soptList, Header } from './components/index.js'

const modules = $ref([EffectFade, Navigation, Pagination, Autoplay])

let urls = $ref([])
let announcementsList = $ref([])

const getBannerList = async () => {
  const data = await homeApi.bannersList({ bannerType: '2' })
  urls = data
}

const getAnnouncementsList = async () => {
  const data = await homeApi.announcementsList()
  announcementsList = data
}

const toLink = (url) => {
  window.open(url, '_blank')
}

let raw = $ref([])
let rawSpots = $ref([])
const newSpots = $ref([])

const handleSpot = () => {
  newSpots.push(raw.splice(0, 3))
  if (raw.length) {
    handleSpot()
  }
}

const getCoinMarketsList = async () => {
  const tickersList = await homeApi.tickersList()
  const marketsList = await homeApi.coinMarketsList()

  raw = tickersList
  rawSpots = [...tickersList]
  handleSpot()
}

onMounted(() => {
  getCoinMarketsList()
  getBannerList()
  getAnnouncementsList()
})

</script>
<style lang="scss" scoped>
  .bg-bnner {
    background-size: 100%;
  }
  .buy-coin-quickly{
    border-top: 20px solid #F7F9FC;
    height: 170px;
    border-bottom: 20px solid #F7F9FC;
    padding: 26px 30px 28px 30px;
  }
  .gonggao{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>
